import React, { Component } from 'react';
import Levels from './levels';
import Freezer from './freezer';
import './stylesheets/fridge.css';
import _ from 'lodash';

class Fridge extends Component {
    state = {
        fridge_categories: ["Dairy and Cheese", "Fruits", "Vegetables"],
        freezer_categories: ["Meat"],
    }
    
    render() { 
        return <div className="Fridge"> 
         <div className="row"><Freezer onRemove={this.props.onRemove} products={_.filter(this.props.products, product => this.state.freezer_categories.includes(product.cluster))}/></div>
         <div className="row"><Levels onRemove={this.props.onRemove} categories={this.state.fridge_categories} products={_.filter(this.props.products, product => this.state.fridge_categories.includes(product.cluster))}/></div>
         </div>;
    }
}
 
export default Fridge;