import React, { Component } from 'react';
import Dropdown from 'react-bootstrap/Dropdown';
import { DropdownSubmenu} from "react-bootstrap-submenu";
import _ from 'lodash';


class Shop extends Component {
    render() { 
        return <Dropdown drop='end'>
        <Dropdown.Toggle variant="success" id="dropdown-basic">
          Shop Here
        </Dropdown.Toggle>
        
        
      
        <Dropdown.Menu>
        {this.props.categories.map(category => <DropdownSubmenu title={category}>
        {_.filter(this.props.options,["cluster", category]).map(option =>
            <Dropdown.Item onClick={() => this.props.onSelect(option)}>
              {option.name}
            </Dropdown.Item>
        )}
            </DropdownSubmenu>
          )}
        </Dropdown.Menu>
      </Dropdown>;
    }
}
 
export default Shop;