import React, { useState } from "react";
import { v4 as uuidv4 } from 'uuid';
import IngredientItem from './ingredient';
import InstructionItem from './instruction';
import {Table,Button,Row,Col} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAdd } from "@fortawesome/free-solid-svg-icons";


export default function Create({ history, match }) {
    const { id } = match.params;
    const isAddMode = !id;
    console.log(isAddMode)
    const [state, setState] = useState({
        name: "",
        amount: "",
        title: "",
        step: "",
        prep_time: "",
        cuisine: ""
    })
    if (!isAddMode) {
        fetch(`http://localhost:5000/record/${id}`)
        .then(response => {
            if (response.ok) {
                return response.json()
            }
            throw response
          }
        )
        .then(data => {
            console.log(data)
            setState({title: data.title})
        }
        );
    }

    
    const [ing_list, setList] = useState([]);
    const [ins_list, setInsList] = useState([]);
    const [image, setImage] = useState(null);

    function handleChange(event){
        const value = event.target.value;
        setState({
            ...state,
            [event.target.name]: value
        });
    }

    function handleImageChange(event) {
        setImage(URL.createObjectURL(event.target.files[0]));
    }

    function handleRemove(id) {
        console.log(id)
        const newList = ing_list.filter((item) => item.id !== id);
        console.log(newList)
        setList(newList);
    }
    function handleStepRemove(id) {
        const newList = ins_list.filter((item) => item.id !== id);
        setInsList(newList);
    }

    function handleAdd() {
        const obj = { id: uuidv4(), name: state.name, amount: state.amount }
        const newList = ing_list.concat(obj);
        console.log(newList)
        setState({
            ...state,
            name: "",
            amount: ""
        });
        setList(newList);
    }

    
    function handleStepAdd() {
        const obj = { id: uuidv4(), step: state.step, image: image }
        const newInsList = ins_list.concat(obj);
        setState({
            ...state,
            step: ""
        });
        setInsList(newInsList);
        document.getElementById("myFile").value = "";
    }
    const handleSubmit = async() => {
        console.log('Submit clicked')
        fetch('http://localhost:5000/record/add', {
        method: 'POST',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            ingredients: ing_list,
            steps: ins_list,
            title: state.title,
            prep_time: state.prep_time,
            cuisine: state.cuisine
        })
        });
    }
    const handleUpdate = async() => {
        console.log('Submit clicked')
        fetch(`http://localhost:5000/record/update/${id}`)
        .then(({data}) => {
            this.setState({title: data.title})
          }
        );
    }

    function onSubmit() {
        return isAddMode
            ? handleSubmit()
            : handleUpdate(id);
    }

    // This following section will display the form that takes the input from the user.
    return (
        <div>
            <h3>{isAddMode ? 'Add a new Recipe' : 'Edit a Recipe'}</h3>
            <Row>
                <Col>
                <h4>Title</h4>
                <input type="text" value={state.title} name="title" onChange={handleChange}/>
                </Col>
                <Col>
                <h4>Prep Time</h4>
                <input type="text" value={state.prep_time} name="prep_time" onChange={handleChange}/>
                </Col>
                <Col>
                <h4>Cuisine</h4>
                <input type="text" value={state.cuisine} name="cuisine" onChange={handleChange}/>
                </Col>
            </Row>
            
            <h4>Ingredients</h4>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Amount</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <IngredientList list={ing_list} onRemove={handleRemove}/>
                    <AddItem
                        state={state}
                        onChange={handleChange}
                        onAdd={handleAdd}
                    />
                </tbody>
            </Table>
            <h4>Instructions</h4>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>Step</th>
                        <th>Image</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <InstructionList list={ins_list} onRemove={handleStepRemove}/>
                    <AddStep
                        state={state}
                        onChange={handleChange}
                        onImageChange={handleImageChange}
                        onAdd={handleStepAdd}
                    />
                </tbody>
            </Table>
            <Button onClick={handleSubmit}>Submit</Button>
        </div>
    );
}

const AddItem = ({ state, onChange, onAdd }) => (
    <tr>
        <td><input type="text" value={state.name} name="name" onChange={onChange} /></td>
        <td><input type="text" value={state.amount} name="amount" onChange={onChange} /></td>
        <td>
        <Button onClick={onAdd} variant="outline-primary">
            <FontAwesomeIcon className="m-2" icon={faAdd} />
        </Button>
        </td>
    </tr>
);

const AddStep = ({ state, onChange, onImageChange, onAdd }) => (
    <tr>
        <td><textarea type="text" value={state.step} name="step" onChange={onChange} rows={5} cols={50} /></td>
        <td><input type="file" onChange={onImageChange} id="myFile" /></td>
        <td>
        <Button onClick={onAdd} variant="outline-primary">
            <FontAwesomeIcon className="m-2" icon={faAdd} />
        </Button>
        </td>
    </tr>
);

const IngredientList = ({ list, onRemove }) => (
    list.map((item) => (
        <IngredientItem item={item} key={item.id} onRemove={onRemove}/>
    ))
);

const InstructionList = ({ list, onRemove }) => (
    list.map((item) => (
        <InstructionItem item={item} key={item.id} onRemove={onRemove}/>
    ))
);



