import React, { Component } from 'react';
import {Button, OverlayTrigger, Popover} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash } from '@fortawesome/free-solid-svg-icons'

class Shelf extends Component {
    render() { 
        return <div class="shelf">
        <div class="upper-left-triangle"></div>
        <div class="upper-right-triangle"></div>
        <div class="back">
        {this.props.products.map(p => 
        <OverlayTrigger
        trigger="click"
        placement={'top'}
        overlay={
            <Popover id='popover-positioned-top'>
            <Popover.Header as="h3">{p.name}</Popover.Header>
            <Popover.Body>
                <Button className="food" variant="danger" onClick={() => this.props.onRemove(p)}>Remove me <FontAwesomeIcon icon={faTrash} /></Button>
            </Popover.Body>
            </Popover>
        }
        >
            <img src={p.image}/>
        </OverlayTrigger>
        )}
        </div>
        <div class="base">
        
        </div>
        <div class="front"></div>
      </div>;
    }
}
 
export default Shelf;