import React, { Component } from 'react';
import {Button, Stack} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck, faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';

class IngredientItem extends Component {
    state = {
        editing: false,
        id: this.props.item.id,
        name: this.props.item.name,
        amount: this.props.item.amount
    }
    render() {
        const isEditing = this.state.editing
        return <tr>
            <td>
                {isEditing
                    ? <input type="text" value={this.state.name} onChange={this.handleChangeName} />
                    : this.state.name
                }
            </td>
            <td>
                {isEditing
                    ? <input type="text" value={this.state.amount} onChange={this.handleChangeAmount} />
                    : this.state.amount
                }
            </td>
            <td>
            <Stack direction="horizontal" gap={3}>
                <Button onClick={this.handleToggle} variant="outline-info">
                <FontAwesomeIcon className="m-2" icon={isEditing ? faCheck: faEdit} />
                </Button>
                <div className="vr" />
                <Button onClick={() => this.props.onRemove(this.state.id)} variant="outline-danger">
                <FontAwesomeIcon className="m-2" icon={faTrash} />
                </Button>
            </Stack>
            </td>
        </tr>;
    }
    handleToggle = () => {
        const editing = !this.state.editing
        this.setState({ editing })
    }

    handleRemove = () => {
        const editing = !this.state.editing
        this.setState({ editing })
    }

    handleEdit = () => {
        const editing = !this.state.editing
        this.setState({ editing })
    }

    handleChangeName = (event) => {
        this.setState({name: event.target.value})
    }

    handleChangeAmount = (event) => {
        this.setState({amount: event.target.value})
    }
}

export default IngredientItem;