import React, { Component } from 'react';
import Level from './level';
import _ from 'lodash';

class Levels extends Component {
    state = { isOpen: false };

    handleToggle = () => {
        this.setState({ isOpen: !this.state.isOpen });
    };

    render() { 
        const isOpen = this.state.isOpen;
        return <div className="Shelves">
        {this.props.categories.map(cat => <Level onRemove={this.props.onRemove} name={cat} products={_.filter(this.props.products,["cluster", cat])} key={cat.id}/>)}
        <div onClick={this.handleToggle} className={`Body ${isOpen ? "Open-Door" : ""}`}></div>
        </div>;
    }
}
 
export default Levels;