import React, { Component } from 'react';
import Shelf from './shelf';
import './stylesheets/cabinet.css';
import _ from 'lodash';

class Cabinet extends Component {
    state = {
        categories: ["Baking", "Pasta and Rice", "Oils"],
    }
    render() { 
        return <div className="cabinet">
            <div class="cabinet-top"></div>
            {this.state.categories.map(cat => <Shelf onRemove={this.props.onRemove} name={cat} products={_.filter(this.props.products,["cluster", cat])} key={cat.id}/>)}
        </div>;
    }
}
 
export default Cabinet;