import React, { Component } from 'react';

class Recipe extends Component {
    render() {
        const { image } = this.props.food; 
        return <div style={{backgroundImage:`url(${image})`}}>
            <div style={{backgroundColor: 'rgba(0, 0, 0, 0)'}}>
            <h1 >{this.props.food.title}</h1>
            <h2>{this.props.food.subtitle}</h2>
            <h2>Instructions</h2>
            <ol>
            {this.props.food.steps.map(step => <li key={step.id}>
                {step.instruction}
                </li>)}
                </ol>
            {/* <img src={this.props.food.image}/> */}
            </div>
      </div>;
    }
}
 
export default Recipe;