import React, { Component } from 'react';
import {Button, OverlayTrigger, Popover} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash } from '@fortawesome/free-solid-svg-icons'


class Freezer extends Component {
    state = { isOpen: false };

    handleToggle = () => {
        this.setState({ isOpen: !this.state.isOpen });
    };

    render() { 
        const isOpen = this.state.isOpen;
        return <div className="Shelves">
            
            {this.props.products.map(p =>
            <OverlayTrigger
            trigger="click"
            placement={'top'}
            overlay={
                <Popover id='popover-positioned-top'>
                <Popover.Header as="h3">{p.name}</Popover.Header>
                <Popover.Body>
                    <Button variant="danger" onClick={() => this.props.onRemove(p)}>Remove me <FontAwesomeIcon icon={faTrash} /></Button>
                </Popover.Body>
                </Popover>
            }
            >
                <img src={p.image}/>
            </OverlayTrigger>
            )}
        <div onClick={this.handleToggle} className={`Freezer ${isOpen ? "Open-Door" : ""}`}>
        </div>
        </div>;
    }
}
 
export default Freezer;