import React, { Component } from 'react';
import Heap from 'heap-js';
import Fridge from './fridge';
import Cabinet from './cabinet';
import Shop from './shop';
import Cart from './cart';
import Suggest from './suggest';
import Create from './addRecipe';
import './stylesheets/kitchen.css';



const stuff = require('../data_scraper/ingredients.json');

class Kitchen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            products: stuff,
            available: [],
            suggestions: [],
            wanted: [],
            categories: ["Meat", "Fruits", "Vegetables", "Herbs", "Baking", "Dairy and Cheese"],
        };
      }
    // state = {
    //     products: [
    //         {category: "Fruits", name: "Peach"},
    //         {category: "Fruits", name: "Apple"},
    //         {category: "Vegetables", name: "Tomato"},
    //         {category: "Vegetables", name: "Cucumber"},
    //         {category: "Meat", name: "Beef"},
    //         {category: "Meat", name: "Salmon"},
    //         {category: "Meat", name: "Shrimp"},
    //         {category: "Oils", name: "Olive Oil"},
    //         {category: "Pasta and Rice", name: "Basmati Rice"},
    //         {category: "Grains and Baking", name: "All-Purpose Flour"},
    //     ],
    //     available: [],
    //     suggestions: [],
    //     wanted: [],
    // }
    render() { 
        return <div className="kitchen">
        <div className="row">
            <div className="col-5"><Cabinet products={this.state.available} onRemove={this.handleRemove}/></div>
            <div className="col-5"><Fridge products={this.state.available} onRemove={this.handleRemove}/></div>
            <div className="col-2"><Cart products={this.state.wanted}/></div>
        </div>
            <div className="row"><Shop options={this.state.products} categories={this.state.categories} onSelect={this.handleShop}/></div>
            {/* <div className="row"><Create/></div> */}
            <div className="row"><Suggest suggestions={this.state.suggestions} onSelect={this.handleSuggest} onWant={this.handleWant}/></div>
        </div>;
    }

    handleShop = (product) => {
        const available = this.state.available;
        available.push(product);
        this.setState({available})
        console.log(available)
    }

    handleWant = (product) => {
        const wanted = this.state.wanted;
        wanted.push(product);
        this.setState({wanted})
    }

    handleRemove = (product) => {
        let available = this.state.available;
        available = available.filter(function(item) {
            return item !== product
        })
        this.setState({available})
    }

    handleSuggest = (number) => {
        const available = this.state.available;
        const maxHeap = new Heap(Heap.maxComparator);
        const meals = require('../data_scraper/meals.json');
        let available_set = new Set(available.map(ing => ing.name))
        for (let i = 0; i < meals.length; i++){
            let required_set = new Set(meals[i].ingredients.map(ing => ing.name))
            let intersect = new Set([...required_set].filter(i => available_set.has(i)));
            maxHeap.push([intersect.size, i])
        }
        const suggestions = [];
        for (let i=0; i<number; i++){
            let [l,j] = maxHeap.pop();
            let required_set = new Set(meals[j].ingredients.map(ing => ing.name))
            let difference = new Set([...required_set].filter(b => !available_set.has(b)));
            suggestions.push([meals[j], [...difference]])
        }
        this.setState({suggestions});
    }
}
 
export default Kitchen;