import React, { Component } from 'react';
import { Button, Row, Card, Nav, ListGroup, ListGroupItem, Tab, Dropdown, ToggleButton, ToggleButtonGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCartPlus, faHourglass } from '@fortawesome/free-solid-svg-icons';
import { Link } from "react-router-dom";


class Suggest extends Component {
    constructor(props) {
        super(props);
        this.state = {
            radioValue: '2'
        };
    }

    render() {
        // const [checked, setChecked] = useState(false);
        // const [radioValue, setRadioValue] = useState('1');
        const radios = [
            { name: '10-20 mins', value: '1' },
            { name: '20-40 mins', value: '2' },
            { name: '+40 mins', value: '3' },
        ];

        return <div className="p-2">
            <Dropdown className="p-1" drop="end" autoClose={false}>
                <Dropdown.Toggle variant="primary" id="dropdown-basic">
                    Get Suggestion
                </Dropdown.Toggle>

                <Dropdown.Menu>
                    <Dropdown.Item>
                        <FontAwesomeIcon className="m-2" icon={faHourglass} />
                        <ToggleButtonGroup type="radio" name="options" defaultValue={1}>
                            <ToggleButton id="tbg-radio-1" value={'1'}>
                                10-20 Min
                            </ToggleButton>
                            <ToggleButton id="tbg-radio-2" value={'2'}>
                                20-40 Min
                            </ToggleButton>
                            <ToggleButton id="tbg-radio-3" value={'3'}>
                                40+ Min
                            </ToggleButton>
                        </ToggleButtonGroup>
                    </Dropdown.Item>
                    <Dropdown.Divider />
                    <Dropdown.Item onClick={() => this.props.onSelect(3)}>Food</Dropdown.Item>
                    <Dropdown.Item href="#/action-2">Desert</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
            <div className="row">
            <Row xs={1} md={3} lg={3} >
                    {this.props.suggestions.map(([suggestion, missings]) =>

                        <Card >
                            <Tab.Container defaultActiveKey="first">

                                <Card.Header>
                                    <Nav variant="tabs" defaultActiveKey="first">
                                        <Nav.Item>
                                            <Nav.Link eventKey="first">Recipe</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item>
                                            <Nav.Link className="text-danger" eventKey="Missing">Missing</Nav.Link>
                                        </Nav.Item>
                                    </Nav>
                                </Card.Header>
                                <Card.Body >

                                    <Tab.Content>
                                        <Tab.Pane eventKey="first">
                                            <Card.Img variant="top" src={suggestion.image} />
                                            <Card.Title>{suggestion.title}</Card.Title>
                                            <Card.Text>
                                                {suggestion.subtitle}
                                            </Card.Text>
                                            <Card.Footer className="text-muted">
                                                <Link
                                                    to={`/recipes/${suggestion.title}`}
                                                    key={suggestion.title}
                                                >
                                                    <Button fixed="bottom" variant="primary">
                                                        Give me more Details
                                                    </Button>
                                                </Link>

                                            </Card.Footer>
                                        </Tab.Pane>
                                        <Tab.Pane eventKey="Missing">
                                            <Card.Text>
                                                <ListGroup className="list-group-flush">
                                                    {missings.map(missing => <ListGroupItem>
                                                        {missing}
                                                        <Button className="float-end" onClick={() => this.props.onWant(missing)} type="reset" ><FontAwesomeIcon icon={faCartPlus} /></Button>
                                                    </ListGroupItem>)}
                                                </ListGroup>
                                            </Card.Text>
                                        </Tab.Pane>
                                    </Tab.Content>


                                </Card.Body>
                            </Tab.Container>
                        </Card>
                    )}
                </Row>
            </div>
            {this.props.suggestions.length ? <Button onClick={() => this.props.onSelect(this.props.suggestions.length + 3)}>See More</Button> : ''}
        </div>;
    }
}

export default Suggest;