import React, { Component } from 'react';
import {Button, Stack} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck, faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';


class InstructionItem extends Component {
    state = {
        editing: false,
        id: this.props.item.id,
        step: this.props.item.step,
        image: this.props.item.image
    }
    render() {
        const isEditing = this.state.editing
        return <tr>
            <td>
                {isEditing
                    ? <textarea type="text" value={this.state.step} onChange={this.handleChangeStep} rows={5} cols={50}/>
                    : this.state.step
                }
            </td>
            <td>
                {isEditing
                    ? <input type="file" onChange={this.handleChangeImage} />
                    : <img style={{maxWidth: "250px"}} src={this.state.image}/>
                }
            </td>
            <td>
            <Stack direction="horizontal" gap={3}>
                <Button onClick={this.handleToggle} variant="outline-info">
                <FontAwesomeIcon className="m-2" icon={isEditing ? faCheck: faEdit} />
                </Button>
                <div className="vr" />
                <Button onClick={() => this.props.onRemove(this.state.id)} variant="outline-danger">
                <FontAwesomeIcon className="m-2" icon={faTrash} />
                </Button>
            </Stack>
            </td>
        </tr>;
    }
    handleToggle = () => {
        const editing = !this.state.editing
        this.setState({ editing })
    }

    handleRemove = () => {
        const editing = !this.state.editing
        this.setState({ editing })
    }

    handleEdit = () => {
        const editing = !this.state.editing
        this.setState({ editing })
    }

    handleChangeStep = (event) => {
        this.setState({step: event.target.value})
    }

    handleChangeImage = (event) => {
        this.setState({
            image: URL.createObjectURL(event.target.files[0])
        })
    }
}

export default InstructionItem;