import './App.css';
import React from 'react';
import Recipe from './components/recipe';
import NavBar from './components/navbar';
import Kitchen from './components/kitchen';
import Create from './components/addRecipe';
import {
  Switch,
  Route
} from "react-router-dom";
const meals = require('./data_scraper/meals.json');


function App() {
  return (<React.Fragment>
    <NavBar/>
      <div className="container">
      <Switch>
      <Route path="/create" render={(props) => (
      <Create/>
      )} />
      <Route path="/edit/:id" component={Create} />
      <Route path="/recipes/:title" render={(props) => (
      <Recipe food={meals.find(obj => obj.title === props.match.params.title)}/>
      )}/>
      <Route exact path="/" component={Kitchen}/>
      </Switch>
      </div>
    </React.Fragment>);
}

export default App;
